import styled from 'styled-components';


export const HomeWrapper = styled.div`
    overflow : hidden;
    width: 960px;
    margin：0 auto;
    margin-left : 200px;
    
    

`;

export const HomeLeft = styled.div`
   	float: left;
	margin-left: 15px;
	padding-top: 30px;
	width: 625px;
	.banner-img {
		width: 625px;
		height: 270px;
	}
`;

export const HomeRight = styled.div`
    width:240px;
    float : right;
`;
export const TopicWrapper = styled.div`
    padding : 20px  0 10px 0;
    overflow:hidden;
    margin-left : -18px;
    border-bottom : 1px solid #dcdcdc;

`;
//border : 1px solid #dcdcdc;
export const TopicItem = styled.div`
    margin-left : 18px;
    margin-bottom : 10px;
    padding : 20px  0 10px 0;
    float:left;
    border-radius : 4px;
    padding-right : 10px;
    
    height:32px;
    line-height:32px;
    font-size : 14px;
    .topic-pic{
        display:block;
        float:left;
        width:32px;
        height:32px;
        margin-right : 10px;

    }

`;

export const ListItem = styled.div`
    overflow: hidden;
    padding : 20px 0;
    border-bottom : 1px solid #dcdcdc;
    .pic{
        width : 125px;
        height:100px;
        float : right;
        display : block;
        border-radius : 10px;
    }
`;
export const ListInfo = styled.div`

    width : 500px;
    float : left;
    .title{
        line-height :27px;
        font-size : 18px;
        font-weight : blod;
        color : #333;
    }
    .desc{
        line-height:24px;
        fontsize : 13px;
        color : #999;
    }
    
`;

export const RecommendWrapper = styled.div`
    margin : 30px 0;
    width:280px;
`;

export const RecommendItem = styled.div`
    width:280px;
    height:50px;
    background : url(${(props)=>props.imgUrl});
    background-size : contain;
`;

export const WriterWrapper = styled.div`
    width : 278px;
    border:1px solid #dcdcdc;
    border-radius:3px;
    height:300px;
    line-height:300px;
    text-align : center;
`;

export const LoadMore = styled.div`
    width : 100%;
    height : 40px;
    line-height:40px;
    margin : 30px 0;
    text-align : center;
    border-radius : 20px;
    color : #fff;
    background : #a5a5a5;
    cursor:pointer;
`
export const BackTop = styled.div`
    position : fixed;
    right:100px;
    bottom :100px;
    width:60px;
    height:60px;
    line-height:60px;
    text-align:center;
    font-size :14px;
    border: 1px solid #969696;
`